<template>
	<div class="game">
		<div class="horizontal">
			<div class="roller">
				<swiper :options="swiperOption" ref="mySwiper" class="myswiper">
					<swiper-slide v-for="(item,index) in horizontallist" :key="index">
						<span class="activetab" v-if="index==activetab">{{item}}</span>
						<span @click="activetab=index" v-else>{{item}}</span>
					</swiper-slide>
				</swiper>
			</div>
			<div class="icon">
				<i class="iconfont icon-icon20"/>
			</div>
		</div>
		<div class="gamelist">
			<div class="list" v-for="item,index in gamelist" :key="index">
				<div class="icon">
					<img :src="item.icon">
				</div>
				<div class="name">
					{{item.name}}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
export default {
	data () {
		return {
			horizontallist:[
				'热门游戏',
				'彩票游戏',
				'真人视讯',
				'电子游戏',
				'体育游戏',
			],
			activetab:'0',
			swiperOption: {
				slidesPerView:4,
				notNextTick: true,
				loop:false,
				autoplay:false,
				grabCursor:true,
			},
			gamelist:[
				{icon:'https://m8.3000ms.com/official/images/g-ssc.png',name:'重庆时时彩'},
				{icon:'https://m8.3000ms.com/official/images/g-hkc.png',name:'六合彩'},
				{icon:'https://m8.3000ms.com/official/images/g-bjsc.png',name:'北京赛车'}
			]
		}
	},
	components:{
		swiper,
		swiperSlide,
	}
}
</script>

<style lang="scss">
.game{
	.horizontal{
		font-size:0px;
		.roller{
			width:90%;
			height:40px;
			line-height: 40px;
			overflow-x:scroll;
			overflow-y: hidden;
			display: inline-block;
			white-space:nowrap;
			font-size:1rem;
			text-align: center;
			.activetab{
				color:#f26971;
			}
		}
		.icon{
			text-align:center;
			width:10%;
			height:40px;
			line-height: 40px;
			display: inline-block;
			vertical-align: top;
		}
	}
	.gamelist{
		.list{
			display: inline-block;
			width:33.3333%;
			font-size:0.88rem;
			text-align:center;
			padding: 7px 10px 6px;
			border:1px solid #e6eaed;
			box-sizing: border-box;
			.icon{
				img{
					height:3rem;
				}
			}
			.name{
				font-weight: 700;
				color:#666;
				margin-bottom: 2px;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
	}
}
</style>



