<template>
	<div class="footer">
		<div class="tab" v-for="item,index in barlist" :key="index">
			<div class="icon">
				<img :src="item.icon" width="30px">
			</div>
			<div class="title">
				{{item.title}}
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			barlist:[
				{icon:'https://m8.3000ms.com/official/images/icon-coin.png',title:'资金管理'},
				{icon:'https://m8.3000ms.com/official/images/icon-record.png',title:'投注记录'},
				{icon:'https://m8.3000ms.com/official/images/icon-service.png',title:'客服'},
				{icon:'https://m8.3000ms.com/official/images/icon-transfer.png',title:'转账'},
				{icon:'https://m8.3000ms.com/official/images/icon-gift.png',title:'优惠活动'}
			]
		}
	}
}
</script>

<style lang="scss">
.footer{
	font-size:0px;
	position: absolute;
	bottom:0px;
	left:0px;
	right:0px;
	height:55px;
	border-top: 1px solid #d3d7da;
	.tab{
		display: inline-block;
		width:20%;
		font-size:0.88rem;
		text-align:center;
		position: relative;
		top:5px;
		.icon{

		}
		.title{
			color:#000;
			opacity:.6
		}
	}
}
</style>



