<template>
	<div class="Home">
		<div class="head">
			<div class="thumb">
				<img :src="logo" height="45px">
			</div>
			<div class="right">
				<div class="userbtn" @click="login">
					登录
				</div>
				<div class="userbtn">
					{{username}}
				</div>
			</div>
		</div>
		<swiper/>
		<game/>
	</div>
</template>

<script>
import swiper from './swiper'
import game from './game'
export default {
	data () {
		return {
			logo:'https://img86.3000ms.com/MSiteOfficial/Logo/2016/07/07/101515879.png',
			username:'试玩账号'
		}
	},
	components:{
		swiper,
		game,
	},
	methods:{
		login(){
			this.$router.push({path:'./login'})
		}
	}
}
</script>

<style lang="scss">
.Home{
	.head{
		background:rgba(2,0,1,.5);
		height:55px;
		line-height: 55px;
		.thumb{
			padding:5px;
			display: inline-block;
		}
		.right{
			float:right;
			position: relative;
			right:5px;
			color:#fff;
			display: inline-block;
			font-size:1.1rem;
			font-weight: 600;
			.userbtn{
				display: inline-block;
				padding-right: 10px;
				text-decoration:underline;
			}
		}
	}
}
</style>



