<template>
	<div class="login">
		<div class="userinfo">
			<div class="input" style="border-bottom:1px solid #ddd">
				<div class="key">
					<i class="iconfont icon-geren"/>
					<div class="text">帐号</div>
				</div>
				<div class="value">
					<input type="text" v-model="user">
				</div>
			</div>
			<div class="input">
				<div class="key">
					<i class="iconfont icon-icon-test"/>
					<div class="text">密码</div>
				</div>
				<div class="value">
					<input type="text" v-model="psw">
				</div>
			</div>
		</div>
		<div class="check">
			<label>
				<input type="checkbox" v-model="remember" class="remember">
				<div class="text">记住密码</div>
			</label>
		</div>
		<div class="confirm" @click="confirm">
			登录
		</div>
		<div class="specialbtn">
			<div class="btn">
				马上注册
			</div>
			<div class="btn">
				忘记密码?
			</div>
			<div class="btn" @click="testplay">
				试玩
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			user:'',
			psw:'',
			remember:false,
		}
	},
	methods:{
		confirm(){

		},
		testplay(){
			this.$router.push({path:'./home'})
		}
	}
}
</script>

<style lang="scss">
.login{
	background: #eceff2;
	position: absolute;
	top:55px;
	bottom:0px;
	left:0px;
	right:0px;
	.userinfo{
		margin:30px 10px 0px;
		background: #fff;
		border-radius: 10px;
		border: 1px solid #ddd;
		.input{
			padding:12px 10px;
			line-height: 30px;
			font-size:0px;
			.key{
				display: inline-block;
				width:20%;
				.text{
					display: inline-block;
					font-size: 0.85rem;
				}
				i{
					font-size:1.3rem;
					position: relative;
					top:2px;
				}
			}
			.value{
				width:80%;
				display: inline-block;
				input{
					outline: none;
					border:none;
					font-size:1.2rem;
					color:#7c838c;
					font-weight: 700;
					width:100%;
				}
			}
		}
	}
	.check{
		margin: 0 15px;
		padding:12px 0;
		.text{
			vertical-align: top;
			display: inline-block;
			font-size:1rem;
			color:#32a1ff;
		}
		.remember{
			height:20px;
			width:20px;
		}
		.remember:before{
			border:1px solid;
			border-color: rgba(0,0,0,.35);
			background: rgba(0,0,0,.1);
			border-radius: 2px;
		}
	}
	.confirm{
		background: #32a1ff;
		color:#fff;
		opacity:.4;
		line-height: 40px;
		width:95%;
		margin:0 auto;
		text-align:center;
		border-radius:4px;
		font-size:1rem;
	}
	.specialbtn{
		font-size:0px;
		margin-top:30px;
		.btn{
			width:33.3%;
			text-align:center;
			display: inline-block;
			font-size:1rem;
			color:#32a1ff;
		}
	}
}
</style>



