<template>
	<div class="swiper">
		<swiper :options="swiperOption" ref="mySwiper" class="myswiper">
			<swiper-slide v-for="(item,index) in swiperlist" :key="index">
				<img :src="item" width="100%" >
			</swiper-slide>
			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<div class="marquee">
			<div class="icon">
				<i class="iconfont icon-gonggaolan"/>
			</div>
			<div class="content">
				<marquee scrollamount="3">
					<span v-for="item,index in marqueelist" :key="index">
						{{item}}
					</span>
				</marquee>
			</div>
		</div>
	</div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
export default {
	data () {
		return {
			swiperOption: {
				notNextTick: true,
				loop:true,
				autoplay:{
					delay:3000,
					disableOnInteraction:false,
				},
				grabCursor:true,
				pagination:{
					el:'.swiper-pagination',
					clickable: true
				},
			},
			marqueelist:[
				'重要通告：VIP入款【浦发银行庞高3373】【工商银行马金山1106】已停用；公司会不定期对银行账号进行更换，请您每次入款前务必登录您的会员账号在我们网站获取最新的入款账号，先提交订单再存款，如转入停用账户造成的资金损失本公司概不负责，如有任何疑问请联系我们24小时在线客服。',
				'凤凰娱乐，新增微信，支付宝扫码入款渠道，每天早上9点至凌晨2点会员只需联系在线客服提供微信或支付宝二维码进行支付，提供支付成功截图至客服，返回＜线上存款＞点击＜银行转账＞选择对应的入款方式和金额提交即可快速到账！',
				'重要通告：VIP入款【巫伟鑫】【民生1395】【杨建磊】【招商6021】已停用；公司会不定期对银行账号进行更换，请您每次入款前务必登录您的会员账号在我们网站获取最新的入款账号，先提交订单再存款，如转入停用账户造成的资金损失本公司概不负责，如有任何疑问请联系我们24小时在线客服。',
			],
			swiperlist:[
				'https://img86.3000ms.com/activity/2018/06/04/181663124.jpg',
				'https://img86.3000ms.com/activity/2018/03/23/171192880.jpg',
				'https://img86.3000ms.com/activity/2018/03/23/162637839.jpg',
				'https://img86.3000ms.com/activity/2018/03/23/162790283.jpg',
				'https://img86.3000ms.com/activity/2018/03/23/163025965.jpg',
				'https://img86.3000ms.com/activity/2018/03/23/162855252.jpg',
			]
		}
	},
	components:{
		swiper,
		swiperSlide,
	}
}
</script>

<style lang="scss">
.swiper{
	.myswiper{
		font-size:0px;
		.swiper-pagination{
			text-align:right;
			.swiper-pagination-bullet{
				background:#fff;
				width:12px;
				height:12px;
				opacity: 0.8;
			}
			.swiper-pagination-bullet-active{
				background: #fff000;
				opacity: 1;
			}
		}
	}
	.marquee{
		font-size:0px;
		background: #f5f7f8;
		color: #4d555f;
		border-bottom: 1px solid #e6eaed;
		height:35px;
		line-height: 35px;
		.icon{
			vertical-align: top;
			text-align:center;
			display: inline-block;
			width: 10%;
			i{
				font-size:1.4rem;
			}
		}
		.content{
			font-size:0.6rem;
			display: inline-block;
			width:90%;
		}
	}
}
</style>



