<template>
	<div class="Login">
		<div class="head">
			<div class="icon">
				<i class="iconfont icon-40"/>
			</div>
			<div class="text">
				欢迎登录使用
			</div>
		</div>
		<login/>
	</div>
</template>

<script>
import login from './login'
export default {
	data () {
		return {
			
		}
	},
	components:{
		login,
	}
}
</script>

<style lang="scss">
.Login{
	.head{
		background: #f8f8f8;
		text-align:center;
		line-height: 55px;
		height:55px;
		border-bottom: 1px solid #c3c6cd;
		box-sizing: border-box;
		.icon{
			display: inline-block;
			position: absolute;
			left:5px;
			padding:0 12px;
			font-weight: 900;
			color:#ef6b73;
			i{
				font-size:1.1rem;
			}
		}
		.text{
			display: inline-block;
			color:#000;
			font-weight: 600;
			font-size:1.25rem;
		}
	}
}
</style>



